@import "./../../style/default.less";

.header{
    background-color: @colorM;
    .header-top{
        height: 60px;
        line-height: 60px;
        padding: 0 20px;
        text-align: right;
        .logo{
            line-height: 60px;
            text-align: left;
            font-size: 18px;
            img{
                height: 40px;
                vertical-align: middle;
                margin-right: 10px;
            }
        }
        a{
            margin-left: 40px;
            color: #f9c700
        }
    }
    .breadcrumb{
        height: 40px;
        line-height: 40px;
        padding: 0 20px;
        border-top: 1px solid #f9c700;
        .breadcrumb-title{
            text-align: center;
            font-size: @fontC;
            &::after{
                position: absolute;
                left: 41%;
                top: 39px;
                content: "";
                border-top: 9px solid @colorM;
                border-right: 12px solid transparent;
                border-left: 12px solid transparent;
            }
        }
        .weather{
            text-align: right;
            .date{
                margin-right: 10px;
            }   
            .weather-img{
                img{
                    height: 15px;
                }
            } 
            .weather-text{
                margin-left: 5px;
            }        
        }
    }
}

// common 页面简单头
.simple-page{
    .header-top{
      background:#1890ff;
      color:@colorM;
    }
    .ant-form,
    .ant-col-12,
    .weather{
      display:none;
    }
  }